import React, { Component } from "react";
import { Row, Col } from "react-bootstrap";
import {
  keydownHandler,
  pointerupHandler,
  pointerdownHandler,
} from "../custom_scripts/keypadHandlers";
import appVariables from "../custom_scripts/variables";

class Keypad extends Component {
  componentDidMount() {
    document.addEventListener("keydown", keydownHandler);
    document.addEventListener("keyup", pointerupHandler);
    document.addEventListener("pointerdown", pointerdownHandler);
    document.addEventListener("pointerup", pointerupHandler);
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", keydownHandler);
    document.removeEventListener("keyup", pointerupHandler);
    document.removeEventListener("pointerdown", pointerdownHandler);
    document.removeEventListener("pointerup", pointerupHandler);
  }

  render() {
    const generateKeypad = (start, end) => {
      const keypads = [];

      for (let id = start; id < end; ++id) {
        const key = appVariables.keys[id];
        keypads.push(
          <Col className="drum-pad" id={key} key={key}>
            {key}
          </Col>
        );
      }

      return <Row>{keypads}</Row>;
    };

    return (
      <>
        {generateKeypad(0, 3)}
        {generateKeypad(3, 6)}
        {generateKeypad(6, 9)}
      </>
    );
  }
}

export default Keypad;
