import appVariables from "./variables";

function setPower() {
  appVariables.powerOn = !appVariables.powerOn;
  if (!appVariables.powerOn) {
    document.getElementById("display-content").innerHTML = `&nbsp`;
    setVolume(0.0);
  } else {
    setVolume(document.getElementById("volume-range").value);
  }
}

function setBankNum() {
  appVariables.bankNum = appVariables.bankNum === 0 ? 1 : 0;
  document.getElementById("display-content").innerHTML =
    appVariables.bankNum === 0 ? "Heater Kit" : "Piano Kit";
  stopAudio();
}

function setVolume(newVolume) {
  appVariables.volume = (newVolume * 1.0) / 100;
}

function stopAudio() {
  appVariables.playingSwitcher.forEach((switcher) => switcher.stop());
  appVariables.playingSwitcher = [];
}

export { setPower, setBankNum, setVolume, stopAudio };
